import OrderForm from '@/components/order-form';

export default function OrderDemoPage() {
  return (
    <div className="min-h-screen bg-gray-50 py-12">
      <div className="container mx-auto px-4">
        <div className="text-center mb-8">
          <h1 className="text-3xl font-bold text-gray-800 mb-4">
            Demo Form Đặt Hoa
          </h1>
          <p className="text-gray-600">
            Test chức năng gửi thông tin đặt hoa sang Discord
          </p>
        </div>
        
        <OrderForm />
        
        <div className="mt-12 max-w-2xl mx-auto bg-white p-6 rounded-lg shadow-md">
          <h2 className="text-xl font-semibold mb-4">Hướng dẫn sử dụng:</h2>
          <ol className="list-decimal list-inside space-y-2 text-gray-700">
            <li>Cập nhật Discord Webhook URL trong file .env</li>
            <li>Điền form đặt hoa ở trên</li>
            <li>Nhấn "Đặt Hoa Ngay" để gửi</li>
            <li>Kiểm tra Discord channel để xem thông báo</li>
          </ol>
          
          <div className="mt-6 p-4 bg-yellow-50 border border-yellow-200 rounded-lg">
            <h3 className="font-semibold text-yellow-800 mb-2">Lưu ý:</h3>
            <p className="text-yellow-700 text-sm">
              Nhớ thay thế DISCORD_WEBHOOK_URL trong file .env bằng webhook URL thực tế từ Discord server của bạn.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}